*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 10px;
    color: white;
}

::-webkit-scrollbar{
    width: 0.8rem;
}

::-webkit-scrollbar-track{
    background-color: rgba(255,255,255,0.4);
}

::-webkit-scrollbar-thumb{
    background-color: rgba(255,255,255,0.6);
}

::-webkit-scrollbar-corner{
    border-radius: 0.4rem;
}

::-webkit-scrollbar-thumb:hover{
    background-color: rgba(20,20,20,0.6);
}

body{
    background-color: #0d1117;
}

.body{
    position: absolute;
    height: 100%;
    width: 100%;

    display: grid;
    grid-template-rows: 10rem 1fr;
    justify-items: center;
}

.top-bar{
    grid-row: 1/2;
    display: grid;
    height: 100%;
    width: 90%;
    align-items: center;
}

.add-note{
    justify-self: start;

    display: grid;
    background-color: #2EA043;
    border: none;
    border-radius: 1rem;
    height: 4rem;
    width: 14rem;
    grid-template-columns: 5rem 1fr;

    align-items: center;
    justify-content: center;
}

.add-note #text{
    justify-self: start;
    font-size: 1.8rem;
    color: white;
}

.message{
    position: absolute;
    left: 50%;
    top: 5rem;
    transform: translate(-50%, -50%);
    font-family: sans-serif;
    font-size: 1.5rem;
    color: gray;
}

.main-section
{
    width: 95%;
    display: grid;

    justify-content: center;
    justify-items: center;
}

.item{
    margin-bottom: 5rem;
    width: 40rem;
    border: rgba(255,255,255,0.4) solid 1px;
    border-radius: 1.5rem;
    background-color: #161B22;

    display: grid;
    grid-template-rows: auto auto;
    justify-content: center;
}

.item .top{
    width: 100%;
    display: grid;
    grid-template-columns: 25rem 5rem;
}

.delete-button{
    height: 3rem;
    width: 3rem;
    display: grid;
    align-items: center;
    justify-items: center;
    border-radius: 1rem;
    justify-self: end;
    margin-top: 3rem;
    border: none;
    background-color: #FB5B63;
}

.delete-button path{
    fill: white;
}

textarea{
    width: 30rem;
    min-width: 30rem;
    max-width: 30rem;
    margin-top: 3rem;
    background: none;
    border: none;
    color: white;
}

textarea:focus{
    outline: none;
}

.heading{
    font-size: 2.2rem;
    font-weight: bold;
    width: 25rem;
    min-width: 25rem;
    max-width: 25rem;

}

.data{
    font-size: 1.8rem;
    margin-bottom: 3rem;
}

